
/* before/after */
.selected {
  -webkit-box-shadow: 0px 0px 3px darkgray;
          box-shadow: 0px 0px 3px darkgray;
}
.unselected{
  opacity: .5;
}
.transparent {
  opacity: .8;
}
.dropable {
  background-color: lightpink;
  border: 1px solid darkorange;
}
.dropable:hover {
  background-color: lightcyan;
}
.selectedContainer {
  -webkit-box-shadow: 1px 1px 3px darkgray;
          box-shadow: 1px 1px 3px darkgray;
}
.V2Empty{
  position:relative;
}
.V2Empty::before {
  content: "";
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  left: 2px;
  top: 2px;
  opacity: .5;
  position: absolute;
  background: lightgreen;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  color: #2c3e50;
}
body{
  margin:0;
}

.testInput {
  width: 100px;
  height: 40px;
  position: relative;
  background: yellow;
  margin: 10px;
}
.editorPart {
  margin: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100vw;
  height: 100vh;
}
.palatte {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  background: #eeeef2;
  border: 1px solid #ccc;
  -webkit-box-shadow: 0 0 2px #000;
  box-shadow: 0 0 2px #000;
}
.editor {
  -webkit-box-flex: 6;
      -ms-flex: 6;
          flex: 6;
  height: 100%;
  overflow-y: auto;
  padding: 0.6em;
  background: #fff;
  border: 1px solid #ccc;
  margin: 0 0.8em;
}
.control {
  -webkit-box-flex: 2;
      -ms-flex: 2;
          flex: 2;
  height: 100%;
  overflow-y: auto;
  overflow-x: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background: #eeeef2;
  border: 1px solid #ccc;
}
.toolbar {
  position: fixed;
  padding: 2px;
  border: 1px solid darkcyan;
  -webkit-box-shadow: 0 1px 1px #000;
          box-shadow: 0 1px 1px #000;
  left: 40%;
  opacity: 0.2;
}
.toolbar:hover {
  opacity: 1;
}
/*  */
@media print {
.palatte {
    display: none;
}
.selected {
    border: none;
}
.editor {
    margin: 0;
    padding: 0 5px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 1px solid #000;
}
}

.dialogWindow {
  position: fixed;
  overflow: none;
  -webkit-box-shadow: 0 0 5px 1px gray;
          box-shadow: 0 0 5px 1px gray;
}
.chat {
  position: relative;
  left: 0;
  top: 0;
  width: 100%;
  height: 250px;
  background: white;
  overflow: auto;
}
.dialog {
  position: relative;
  height: 20px;
  margin-top: 10px;
}
.message {
  padding: 0px 4px;
  border-radius: 5px;
}
.message > pre {
  margin: 0;
}
.right {
  background: lightgreen;
  float: right;
  margin-right: 10px;
}
.left {
  background: lightblue;
  margin-left: 10px;
  float: left;
}

.frame[data-v-a0416f22] {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}
.frame > .el-container[data-v-a0416f22] {
  height: 100%;
  width: 100%;
}
